@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

:root {
    --background-color: #1e1e1e;
    --text-color: #ffffff;
    --accent-color: #c2dbed;
    --secondary-color: #2c2c2c;
    --hover-color: #3a3a3a;
    --border-color: rgba(255, 255, 255, 0.1);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Inter', sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
    line-height: 1.6;
}

.resume-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 16px 0;
    border-bottom: 1px solid var(--border-color);
    margin-bottom: 24px;
}

.header-content {
    display: flex;
    flex-direction: column;
}

.name-and-social {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
}

.header-content h1 {
    font-size: 24px;
    font-weight: 600;
    margin-right: 16px;
}

.social-links {
    display: flex;
    gap: 12px;
}

.social-link {
    color: var(--text-color);
    transition: color 0.2s ease;
}

.social-link:hover {
    color: var(--accent-color);
}

.header-content p {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.6);
}

.header-actions {
    display: flex;
    align-items: center;
    gap: 16px;
}

.nav-link {
    color: var(--text-color);
    text-decoration: none;
    font-size: 14px;
    font-weight: 500;
    transition: color 0.2s ease;
}

.nav-link:hover {
    color: var(--accent-color);
}

.pdf-container {
    width: 100%;
    height: calc(100vh - 180px);
    margin-bottom: 24px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    overflow: hidden;
}

.pdf-viewer {
    width: 100%;
    height: 100%;
    border: none;
}

.about-me {
    background-color: var(--secondary-color);
    padding: 24px;
    margin-top: 24px;
    border-radius: 8px;
}

.about-me h2 {
    font-size: 18px;
    margin-bottom: 16px;
    color: var(--text-color);
    font-weight: 600;
}

.about-me p {
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 16px;
    color: rgba(255, 255, 255, 0.8);
}

.collapsible {
    background-color: transparent;
    color: var(--text-color);
    cursor: pointer;
    padding: 0;
    border: none;
    text-align: left;
    outline: none;
    font-size: 14px;
    display: inline;
    margin: 0;
    font-weight: 500;
  }
  
  
  .collapsible-content {
    overflow: hidden;
    transition: max-height 0.3s ease-out;
  }
  
  .collapsible-content p {
    padding: 0;
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 16px;
    color: rgba(255, 255, 255, 0.8);
  }
  
  
  .collapsible-content.expanded {
    max-height: 1000px; 
  }

.footer {
    text-align: center;
    padding: 16px 0;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.6);
    margin-top: 24px;
    border-top: 1px solid var(--border-color);
}

@media (max-width: 768px) {
    .header {
        flex-direction: column;
        align-items: flex-start;
    }

    .header-actions {
        margin-top: 16px;
    }

    .name-and-social {
        flex-direction: column;
        align-items: flex-start;
    }

    .social-links {
        margin-top: 8px;
    }

    .pdf-container {
        height: calc(100vh - 240px);
    }
}
.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .popup-content {
    background-color: rgb(201, 201, 201);
    padding: 3rem;
    border-radius: 12px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
    text-align: center;
    max-width: 600px;
    width: 90%;
  }
  
  .popup-content h2 {
    margin-top: 0;
    color: #333;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }
  
  .popup-content p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    color: #555;
  }
  
  .popup-link {
    display: block;
    font-size: 2rem;
    color: #007bff;
    text-decoration: none;
    margin: 2rem 0;
    padding: 1rem;
    background-color: rgba(0, 123, 255, 0.1);
    border-radius: 8px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .popup-link:hover {
    background-color: rgba(0, 123, 255, 0.2);
    color: #0056b3;
  }
  
  .popup-actions {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }
  
  
  
  .popup-button.secondary {
    background-color: #6c757d;
    color: white;
  }
  
  .popup-button.secondary:hover {
    background-color: #545b62;
  }
  
  @media (max-width: 768px) {
    .popup-content {
      padding: 2rem;
    }
  
    .popup-content h2 {
      font-size: 2rem;
    }
  
    .popup-content p {
      font-size: 1rem;
    }
  
    .popup-link {
      font-size: 1.5rem;
    }
  
    .popup-button {
      font-size: 1rem;
      padding: 0.5rem 1rem;
    }
  }